import React from "react";
import StyledText from "./StyledText.js";
import softBody from "./img/softBody.png";
import hrlOlPortrait from "./img/hrlOlPortrait.PNG";
import acaPortrait from "./img/acaPortrait.png";
import unfilteredPortrait from "./img/unfilteredPortrait.png";
import WindowComponent from "./WindowComponent";
import ResearchCard from "./ResearchCard";

class ResearchView extends WindowComponent {
  render() {
    function renderSimulation() {
      const title = "Simulation and Control of Soft-Bodied Creatures";
      const description =
        "Explores the use of covariance matrix adaptation evolution strategy to find control policies for soft bodied creatures with various body plans and muscle actuation patterns";
      const contributor = "Chunlok Lo, Greg Turk";
      const link = "https://chunloklo.github.io/softbody_gait_discovery/";
      const linkText = "Project Webpage";
      const image = softBody;

      return (
        <ResearchCard
          image={image}
          title={title}
          contributor={contributor}
          description={description}
          link={link}
          linkText={linkText}
        />
      );
    }

    function renderHRL() {
      const title = "Hierarchical Reinforcement Learning with Option Learning";
      const description =
        "Explored combining hierarchical reinforcement learning with option learning methdologies to improve exploration";
      const contributor = "Chunlok Lo, Eric Liu, Matthew Gombolay";
      const link = "http://chunloklo.github.io/pdf/hrl-ol-presentation.pdf";
      const linkText = "Presentation";
      const image = hrlOlPortrait;

      return (
        <ResearchCard
          image={image}
          title={title}
          contributor={contributor}
          description={description}
          link={link}
          linkText={linkText}
        />
      );
    }

    function renderACA() {
      const title = "Agile Communication Architectures";
      const contributor = "Georgia Tech Vertically Integrated Project Team";
      const description =
        "Apply machine learning to cognitive radios to exploit the specturm";
      const link =
        "https://www.vip.gatech.edu/teams/agile-communication-architectures";
      const linkText = "Team Page";
      const image = acaPortrait;

      return (
        <ResearchCard
          image={image}
          title={title}
          contributor={contributor}
          description={description}
          link={link}
          linkText={linkText}
        />
      );
    }

    function renderUnfiltered() {
      const title =
        "Unfiltered: Automatic Identification and Inversion of Social Media Photography Filters";
      const contributor = "Chunlok Lo, Mert Dumenci, Cem Gokumen, Joel Ye";
      const description =
        "Apply machine learning to inverse common instagram filters. Done as part of CS4476 Computer Vision at Georgia Tech";
      const link =
        "https://team-vision2020.github.io/";
      const linkText = "Project Page";
      const image = unfilteredPortrait;

      return (
        <ResearchCard
          image={image}
          title={title}
          contributor={contributor}
          description={description}
          link={link}
          linkText={linkText}
        />
      );
    }

    return (
      <div style={{marginTop: 50}}>
        <div>
          <StyledText
            style={{fontWeight: 400, fontSize: 32, textAlign: "left"}}
          >
            Research
          </StyledText>
        </div>
        {renderSimulation()}
        {renderHRL()}
        {renderACA()}
        {renderUnfiltered()}
      </div>
    );
  }
}
export default ResearchView;
