import React from "react";
import StyledText from "./StyledText.js";
import WindowComponent from "./WindowComponent";

class ResearchCard extends WindowComponent {
  render() {
    const imageStyle = {
      width: 150,
      height: 150,
      marginRight: 40
    };

    var cardStyle = {display: "flex", marginTop: 40};
    if (this.state.width < 800) {
      console.warn("hELLO");
      cardStyle = {...cardStyle, flexDirection: "column"};
    }

    const titleStyle = {fontWeight: 300, fontSize: 28, textAlign: "left"};
    const contributorStyle = {fontWeight: 100, fontSize: 18, textAlign: "left"};
    const descriptionStyle = {fontWeight: 100, fontSize: 18, textAlign: "left"};

    var title = this.props.title;
    var contributor = this.props.contributor;
    var image = this.props.image;
    var description = this.props.description;
    var linkText = this.props.linkText;
    var link = this.props.link;

    return (
      <div style={cardStyle}>
        <img style={imageStyle} src={image} alt="portrait" />
        <div>
          <StyledText style={titleStyle}>{title}</StyledText>
          <StyledText style={contributorStyle}>
            <i>{contributor}</i>
          </StyledText>
          <StyledText style={descriptionStyle}>{description}</StyledText>
          <StyledText
            style={{
              fontWeight: 100,
              fontSize: 18,
              textAlign: "left",
              marginTop: 10
            }}
          >
            <a href={link} target="_blank" rel="noopener noreferrer">
              {linkText}
            </a>
          </StyledText>
        </div>
      </div>
    );
  }
}
export default ResearchCard;
