import React from "react";
import portraitCropped from "./img/portraitCropped.jpg";
import ResearchView from "./ResearchView";
import StyledText from "./StyledText.js";
import WindowComponent from "./WindowComponent";

class MainScreen extends WindowComponent {
  render() {
    const width = 250;

    const imageStyle = {
      height: width,
      borderRadius: width / 2
    };

    const boxStyle = {
      marginRight: 100
    };

    var name = "Chunlok Lo";
    var intro =
      "I am currently an undergraduate student in Computer Science at Georgia Tech. My current research interests are reinforcement learning, computer animation, and robotics. I have also interned at Facebook, Riot Games, Ab Initio, and Home Depot";

    var wideView = (
      <div style={{display: "flex"}}>
        <div style={boxStyle}>
          <StyledText style={{fontWeight: 400, fontSize: 48, margin: 10}}>
            {name}
          </StyledText>
          <StyledText
            style={{fontWeight: 300, fontSize: 18, textAlign: "left"}}
          >
            {intro}
          </StyledText>
          <StyledText style={{fontWeight: 300, fontSize: 18, marginTop: 20}}>
            Email: chunlok.lo1@gmail.com
          </StyledText>
          <StyledText style={{fontWeight: 300, fontSize: 18}}>
            <a href="http://chunloklo.github.io/pdf/cv.pdf" target="_blank" rel="noopener noreferrer">
              CV
            </a>{" "}
            /{" "}
            <a href="http://github.com/chunloklo/" target="_blank" rel="noopener noreferrer">
              Github
            </a>{" "}
            /{" "}
            <a href="https://www.linkedin.com/in/chunloklo/" target="_blank" rel="noopener noreferrer">
              LinkedIn{" "}
            </a>
          </StyledText>
        </div>
        <img style={imageStyle} src={portraitCropped} alt="portrait" />
      </div>
    );

    var narrowView = (
      <div>
        <StyledText style={{fontWeight: 400, fontSize: 48, margin: 10}}>
          {name}
        </StyledText>
        <div style={{margin: 20}}>
          <img style={imageStyle} src={portraitCropped} alt="portrait" />
        </div>
        <StyledText style={{fontWeight: 300, fontSize: 18, textAlign: "left"}}>
          {intro}
        </StyledText>
        <StyledText style={{fontWeight: 300, fontSize: 18, marginTop: 20}}>
          Email: chunlok.lo1@gmail.com
        </StyledText>
        <StyledText style={{fontWeight: 300, fontSize: 18}}>
          <a href="http://chunloklo.github.io/pdf/cv.pdf" target="_blank" rel="noopener noreferrer">
            CV
          </a>{" "}
          /{" "}
          <a href="http://github.com/chunloklo/" target="_blank" rel="noopener noreferrer">
            Github
          </a>{" "}
          /{" "}
          <a href="https://www.linkedin.com/in/chunloklo/" target="_blank" rel="noopener noreferrer">
            LinkedIn{" "}
          </a>
        </StyledText>
      </div>
    );

    return (
      <div>
        <div style={{margin: "10%"}}>
          {this.state.width > 800 ? wideView : narrowView}
          <ResearchView />
        </div>
      </div>
    );
  }
}
export default MainScreen;
